import React from 'react'
import Navbar from './Navbar';
import { Component } from 'react';
import Request from './Request';
import Deneme from './Deneme';

class Requests extends Component {
    constructor(props) {
        super(props);
        this.props = {
            addRequests: [],
            remRequests: []
        }
    }

    componentWillMount() {
        this.setState({addRequests: [
            {
                id: 1,
                lat: 47.88,
                long: 50.55,
                text: "So many animals"
            },
            {
                id: 2,
                lat: 49.88,
                long: 52.55,
                text: "So many animals"
            }
        ]});
        this.setState({remRequests: [
            {
                id: 1,
                lat: 47.88,
                long: 50.55,
                text: "No animals"
            },
            {
                id: 2,
                lat: 49.88,
                long: 52.55,
                text: "No animals"
            },
            {
                id: 3,
                lat: 47.88,
                long: 50.55,
                text: "No animals"
            },
            {
                id: 4,
                lat: 47.88,
                long: 50.55,
                text: "No animals"
            },
            {
                id: 5,
                lat: 47.88,
                long: 50.55,
                text: "No animals"
            },
            {
                id: 6,
                lat: 47.88,
                long: 50.55,
                text: "No animals"
            },
            {
                id: 7,
                lat: 47.88,
                long: 50.55,
                text: "No animals"
            },
            {
                id: 8,
                lat: 47.88,
                long: 50.55,
                text: "No animals"
            }
        ]});
    }

    
    render() {
        const {addRequests, remRequests} = this.state;
        return (
            <div>
                <Navbar/>
                <br/>
                <div className='AR'>
                    <h2>Point Addition Requests</h2>
                    {addRequests.map(item => {
                        return <Request
                            text = {item.text}
                            lat = {item.lat}
                            long = {item.long}
                        />

                    })}
                </div>

                <div className='RR'>
                    <h2>Point Removal Requests</h2>
                    {remRequests.map(item => {
                        return <Request
                        text = {item.text}
                        lat = {item.lat}
                        long = {item.long}
                        />

                    })}  
                </div>
            </div>
        )
    }
    
}

export default Requests;
