import React from 'react'
import { Component } from 'react';


class Request extends Component {
 
    render() {
        const {text, lat, long} = this.props;
        return (
            <div className='block'>
                
                <p>Latitude : {lat}</p>
                <p>Longitude : {long}</p>
                <p>Reason : {text}</p>      
                <br/>
            </div>
        )
    }
    
}

export default Request;
